.container {
  user-select: none;
  box-sizing: border-box;
  margin: 0 auto;
  display: flex;

  box-shadow: 0 0 10px rgba(0, 0, 0, 0.02);
  transition: transform 0.1s linear, box-shadow 0.1s linear;
  cursor: pointer;

  position: relative;
  padding: 23px;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.05);
  }

  .photo {
    text-align: center;
    height: 124px;
    width: 124px;
    border: 1px #DFDFDF solid;
    border-radius: 14px;
    margin-right: 23px;
  }
  .noPhoto{
    padding: 15px;

  }

  .about {
    font-size: 14px;

    .level {
      font-size: 18px;
      font-weight: 600;
    }


  }

}
