
.container::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.container {
  max-width: 1280px;
  height: 650px;
  padding: 37px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  margin: 0 auto;
  overflow: auto;
  position: relative;
  gap: 34px;


}


