.container {
  position: fixed;
  top: 50%;
  left: 50%;

  z-index: 50;

  .overlay {
    position: fixed;
    height: 100vh;
    width: 100vw;
    left: 0;
    top: 0;
    background-color: black;
    opacity: 0.4;

  }

  .window {
    position: absolute;
    transform: translate(-50%, -44%);
    padding: 25px;
    background-color: #F4F6F7;
    height: 554px;
    width: 550px;
    border-radius: 14px;

    .topWindow {
      display: flex;

      .photo {
        height: 200px;
        width: 200px;
        text-align: center;
        border: 1px solid #9F9F9F;
        border-radius: 14px;
        margin: 0 30px 30px 0;
      }
      .noPhoto{
        padding: 15px;

      }

      .about {
        font-size: 17px;


        .level {
          font-size: 21px;
          font-weight: 600;
        }


      }
    }




  }

}